import React from "react";
import styles from "../Styles/Aboutme.module.css";
const Aboutme = () => {
  return (
    <div id="about">
      <div className={styles.main}>
        <div className={styles.aboutDiv}>
          <h1 className={styles.about}>About Me</h1>
        </div>
        <div className={styles.description}>
          <div className={styles.img}>
            <img
              src="Images/Rani_photo_new.jpg"
              className={styles.rani}
              alt=""
            />
          </div>
          <div className={styles.lines}>
            <h2 className={styles.hi}>Hi There ! I Am Shibangi Das</h2>
            <p className={styles.para}>
              A full stack web developer with a very creative mindset,who
              ventured into this world because of their love towards technology.
              As a determined person who, otherthan full stack development have
              interest in reading books,discovering about new things and leaning
              about forefront.
              <div>
                <a href={"../Resume/Shibangi_Das_Resume.pdf"} download>
                  {" "}
                  <button
                    onClick={() =>
                      window.open(
                        "https://drive.google.com/file/d/1RrkUgSzYMVc3eC13_mecl-y4IwTc6GXS/view?usp=share_link"
                      )
                    }
                    className={styles.resume}
                  >
                    Resume
                  </button>
                </a>
              </div>
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Aboutme;
