import React from "react";
import styles from "../Styles/Contact.module.css";
const Contact = () => {
  return (
    <div className={styles.main} id="contact">
      <div className={styles.subDiv}>
        <h1 className={styles.get}>Get In Touch</h1>
        <div className={styles.form}>
          <form>
            <div className={styles.formDiv}>
              <input
                type="text"
                placeholder="Please enter your name"
                className={styles.inputDiv}
              />
            </div>
            <div className={styles.formDiv}>
              <input
                type="email"
                placeholder="Please enter your email"
                className={styles.inputDiv}
              />
            </div>
            <div className={styles.formDiv}>
              <input
                type="text"
                placeholder="Your message"
                className={styles.inputDiv}
              />
            </div>
            <button className={styles.submit}>Submit</button>
          </form>
        </div>
        <div className={styles.align}>
          <div className={styles.phoneNo}>
            <img src="../Images/Contact.png" className={styles.phn}/>
            <button className={styles.no}>+91 7750901077</button>
          </div>

          <div className={styles.gmailDiv}>
            <img
              src="../Images/Gmail.png"
              alt="gmail"
              className={styles.gmail}
            />
            <button className={styles.btn}>shibangidas109@gmail.com</button>
          </div>
          <div className={styles.linkedinDiv}>
            <a href="https://www.linkedin.com/in/shibangi-das/" target="_blank">
              <img
                src="../Images/linkedinImg.png"
                alt=""
                className={styles.linkedin}
              />
            </a>
          </div>
          <div className={styles.githubDiv}>
            <a href="https://github.com/ShibangiD1996" target="_blank">
              <img
                src="../Images/gitnew.png"
                alt=""
                className={styles.github}
              />
            </a>
          </div>
        </div>
      </div>

      <img
        src="../Images/contactnew.jpg"
        alt="contact"
        className={styles.contactImg}
      />
    </div>
  );
};

export default Contact;
